/* eslint-disable react/no-unused-prop-types */
/* eslint-disable react/destructuring-assignment */
import React from 'react';
import PropTypes from 'prop-types';
import Typist from 'react-typist';
import { withSlides } from 'gatsby-plugin-metakronisms-content/dist/Slides';
import Card from '../Card/Card';
import styles from './TypistSlide.module.scss';

const TypistSlide = (props) => {
  const {
    className,
    children,
    startDelay,
    nextDelay,
    episodeContext: {
      nextSlide,
    },
  } = props;
  const [currentChild, setCurrentChild] = React.useState(0);
  const next = React.useCallback(() => setTimeout(nextSlide, nextDelay), [nextDelay, nextSlide]);

  const handleTypingDone = React.useCallback(() => setCurrentChild((prev) => {
    const count = ((children && 1) && children.length) || (children && 1) || 1;
    if (prev >= count - 1) {
      next();
      return prev;
    }
    return prev + 1;
  }), [children, next]);

  let content = null;
  if (!Array.isArray(children)) {
    content = (
      <Typist
        startDelay={startDelay}
        onTypingDone={handleTypingDone}
        className={styles.slide__typist}
        // cursor={{
        //   hideWhenDone: true,
        //   element: ' ',
        // }}
      >
        {children}
      </Typist>
    );
  } else {
    content = [
      ...children.slice(0, currentChild),
      (
        <Typist
          startDelay={currentChild === 0 ? startDelay : 0}
          key={`typist-${currentChild}`}
          className={styles.slide__typist}
          onTypingDone={handleTypingDone}
          // cursor={{
          //   // hideWhenDone: true,
          //   element: ' ',
          // }}
        >
          {children[currentChild]}
        </Typist>
      ),
    ];
  }

  const classNames = [
    className,
    styles.slideTypist,
  ];
  return (
    <div className={styles.slide}>
      <Card className={classNames.filter(Boolean).join(' ')}>
        {content}
      </Card>
    </div>
  );
};

TypistSlide.displayName = 'MKTypistSlide';

TypistSlide.propTypes = {
  startDelay: PropTypes.number,
  nextDelay: PropTypes.number,
  className: PropTypes.string,
  children: PropTypes.node,
  episodeContext: PropTypes.shape({
    nextSlide: PropTypes.func.isRequired,
  }).isRequired,
};

TypistSlide.defaultProps = {
  startDelay: 1000,
  nextDelay: 1000,
  className: null,
  children: null,
};

export default TypistSlide;

export const WithSlides = withSlides(TypistSlide);
